"use client"

import { useState, useRef } from "react"
import { HologramCard } from "@/components/hologram-card"
import { ChevronDown, Zap, Copy, Check } from "lucide-react"
import { cn } from "@/lib/utils"

const agents = [
  { id: "gpt-verify", name: "GPT-Verify", description: "General purpose verification" },
  { id: "code-audit", name: "Code-Audit", description: "Smart contract analysis" },
  { id: "data-check", name: "Data-Check", description: "Data integrity validation" },
  { id: "fact-scan", name: "Fact-Scan", description: "Factual accuracy checker" },
]

export default function RunAgentPage() {
  const [selectedAgent, setSelectedAgent] = useState(agents[0])
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [input, setInput] = useState("")
  const [output, setOutput] = useState("")
  const [isRunning, setIsRunning] = useState(false)
  const [showProofButton, setShowProofButton] = useState(false)
  const [proofCreated, setProofCreated] = useState(false)
  const [copied, setCopied] = useState(false)
  const outputRef = useRef<HTMLDivElement>(null)

  const handleRun = async () => {
    if (!input.trim()) return

    setIsRunning(true)
    setOutput("")
    setShowProofButton(false)
    setProofCreated(false)

    // Simulate typewriter output
    const response =
      `[${selectedAgent.name}] Analysis Complete\n\n` +
      `Input Hash: 0x${Math.random().toString(16).slice(2, 18)}\n` +
      `Verification Status: PASSED\n` +
      `Confidence Score: ${(95 + Math.random() * 5).toFixed(2)}%\n\n` +
      `--- Detailed Analysis ---\n\n` +
      `The provided input has been processed through our verification pipeline.\n` +
      `All integrity checks have passed successfully.\n` +
      `Cross-reference validation: 3/3 nodes confirmed.\n\n` +
      `Timestamp: ${new Date().toISOString()}\n` +
      `Agent ID: ${selectedAgent.id}-${Math.random().toString(36).slice(2, 8)}`

    for (let i = 0; i <= response.length; i++) {
      await new Promise((resolve) => setTimeout(resolve, 15))
      setOutput(response.slice(0, i))
    }

    setIsRunning(false)
    setShowProofButton(true)
  }

  const handleCreateProof = () => {
    setProofCreated(true)
  }

  const handleCopy = () => {
    navigator.clipboard.writeText(output)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">
            <span className="neon-text-cyan">Run</span> Agent
          </h1>
          <p className="text-muted-foreground">Execute AI verification tasks and generate blockchain proofs</p>
        </div>

        {/* Agent Selection */}
        <HologramCard className="p-6 mb-6" glowColor="cyan">
          <label className="block text-sm text-muted-foreground mb-2">Select Agent</label>
          <div className="relative">
            <button
              onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              className={cn(
                "w-full p-4 rounded-lg text-left transition-all duration-300",
                "bg-background/50 border border-neon-cyan/30",
                "hover:border-neon-cyan hover:shadow-[0_0_15px_var(--neon-cyan)]",
                "flex items-center justify-between",
                isDropdownOpen && "border-neon-cyan shadow-[0_0_15px_var(--neon-cyan)]",
              )}
            >
              <div>
                <div className="font-semibold text-neon-cyan">{selectedAgent.name}</div>
                <div className="text-sm text-muted-foreground">{selectedAgent.description}</div>
              </div>
              <ChevronDown
                className={cn(
                  "w-5 h-5 text-neon-cyan transition-transform duration-300",
                  isDropdownOpen && "rotate-180",
                )}
              />
            </button>

            {isDropdownOpen && (
              <div className="absolute top-full left-0 right-0 mt-2 rounded-lg border border-neon-cyan/30 bg-background/95 backdrop-blur-xl overflow-hidden z-20 shadow-[0_0_30px_var(--neon-cyan)]">
                {agents.map((agent) => (
                  <button
                    key={agent.id}
                    onClick={() => {
                      setSelectedAgent(agent)
                      setIsDropdownOpen(false)
                    }}
                    className={cn(
                      "w-full p-4 text-left transition-all duration-200",
                      "hover:bg-neon-cyan/10",
                      selectedAgent.id === agent.id && "bg-neon-cyan/20",
                    )}
                  >
                    <div className="font-semibold">{agent.name}</div>
                    <div className="text-sm text-muted-foreground">{agent.description}</div>
                  </button>
                ))}
              </div>
            )}
          </div>
        </HologramCard>

        {/* Input */}
        <HologramCard className="p-6 mb-6" glowColor="purple">
          <label className="block text-sm text-muted-foreground mb-2">Input Data</label>
          <div className="relative">
            <textarea
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Enter data to verify..."
              className={cn(
                "w-full h-40 p-4 rounded-lg resize-none",
                "bg-background/50 border border-neon-purple/30",
                "focus:border-neon-purple focus:shadow-[0_0_15px_var(--neon-purple)]",
                "focus:outline-none transition-all duration-300",
                "placeholder:text-muted-foreground/50",
              )}
            />
            <div className="absolute bottom-2 right-2 text-xs text-muted-foreground">{input.length} characters</div>
          </div>
        </HologramCard>

        {/* Run Button */}
        <div className="flex justify-center mb-8">
          <button
            onClick={handleRun}
            disabled={isRunning || !input.trim()}
            className={cn(
              "relative px-12 py-4 rounded-xl font-bold uppercase tracking-wider",
              "bg-gradient-to-r from-neon-cyan/20 to-neon-purple/20",
              "border-2 border-neon-cyan",
              "shadow-[0_0_30px_var(--neon-cyan)]",
              "hover:shadow-[0_0_50px_var(--neon-cyan),0_0_80px_var(--neon-purple)]",
              "transition-all duration-300",
              "disabled:opacity-50 disabled:cursor-not-allowed",
              "group overflow-hidden",
            )}
          >
            <span className="relative z-10 flex items-center gap-2">
              <Zap className={cn("w-5 h-5", isRunning && "animate-pulse")} />
              {isRunning ? "Processing..." : "Run Agent"}
            </span>
            {/* Ripple effect */}
            <div className="absolute inset-0 flex items-center justify-center">
              <div className={cn("w-4 h-4 rounded-full bg-neon-cyan", isRunning && "animate-ping")} />
            </div>
          </button>
        </div>

        {/* Output Terminal */}
        {(output || isRunning) && (
          <HologramCard className="p-6 mb-6" glowColor="green">
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-2">
                <div className="w-3 h-3 rounded-full bg-red-500" />
                <div className="w-3 h-3 rounded-full bg-yellow-500" />
                <div className="w-3 h-3 rounded-full bg-green-500" />
                <span className="ml-2 text-sm text-muted-foreground">Terminal Output</span>
              </div>
              {output && (
                <button onClick={handleCopy} className="p-2 hover:bg-neon-cyan/10 rounded transition-colors">
                  {copied ? (
                    <Check className="w-4 h-4 text-neon-green" />
                  ) : (
                    <Copy className="w-4 h-4 text-muted-foreground" />
                  )}
                </button>
              )}
            </div>
            <div
              ref={outputRef}
              className={cn(
                "font-mono text-sm p-4 rounded-lg min-h-[200px]",
                "bg-black/50 border border-neon-green/30",
                "overflow-auto",
              )}
            >
              <pre className="text-neon-green whitespace-pre-wrap">
                {output}
                {isRunning && <span className="animate-pulse">▌</span>}
              </pre>
            </div>
          </HologramCard>
        )}

        {/* Create Proof Button */}
        {showProofButton && (
          <div className="flex justify-center">
            <button
              onClick={handleCreateProof}
              disabled={proofCreated}
              className={cn(
                "relative px-8 py-4 rounded-xl font-bold uppercase tracking-wider",
                "bg-gradient-to-r from-neon-green/20 to-neon-cyan/20",
                "border-2 border-neon-green",
                "shadow-[0_0_20px_var(--neon-green)]",
                "hover:shadow-[0_0_40px_var(--neon-green)]",
                "transition-all duration-300",
                "overflow-hidden group",
                proofCreated && "bg-neon-green/30",
              )}
            >
              {/* Circuit trace animation */}
              <svg className="absolute inset-0 w-full h-full pointer-events-none">
                <path
                  d="M0,25 Q50,10 100,25 T200,25"
                  fill="none"
                  stroke="var(--neon-green)"
                  strokeWidth="1"
                  strokeDasharray="200"
                  className="animate-[circuit-trace_2s_linear_infinite]"
                  style={{ opacity: 0.5 }}
                />
              </svg>
              <span className="relative z-10">{proofCreated ? "✓ Proof Created" : "Create Blockchain Proof"}</span>
            </button>
          </div>
        )}

        {proofCreated && (
          <HologramCard className="mt-6 p-6" glowColor="green">
            <div className="text-center">
              <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-neon-green/20 flex items-center justify-center">
                <Check className="w-8 h-8 text-neon-green" />
              </div>
              <h3 className="text-xl font-bold neon-text-green mb-2">Proof Successfully Created</h3>
              <p className="text-muted-foreground mb-4">Your verification has been recorded on the blockchain</p>
              <div className="font-mono text-sm bg-black/30 p-3 rounded-lg break-all">
                IPFS CID: Qm{Math.random().toString(36).slice(2, 20)}...
              </div>
            </div>
          </HologramCard>
        )}
      </div>
    </div>
  )
}
